export default class OfflineQueue {
  constructor() {
    this.isEnabled = false;
    this.unTrackedEvents = [];
  }

  push(event, options) {
    if (!this.isEnabled) this.unTrackedEvents.push({ event, options });
  }

  trackAll(trackFn, newDefaults) {
    this.unTrackedEvents.forEach(({ event, options }) =>
      trackFn(event, { ...options, ...newDefaults })
    );
    this.isEnabled = true;
    this.unTrackedEvents = [];
  }
}